:root {
  --timing-function: cubic-bezier(.4, 0, .2, 1);
  --animation-duration: 200ms;
}

* {
  box-sizing: border-box;
}

body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #212121;
  background-color: #fafafa;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen-Sans, Ubuntu, Cantarell, Helvetica Neue, sans-serif;
  line-height: 1.5;
}

.input-wrap {
  text-align: center;
  background-color: #07acb8;
  padding-top: 5px;
  padding-bottom: 5px;
}

input {
  vertical-align: middle;
  padding: 4px 5px;
  font-family: sans-serif;
}

.btn {
  cursor: pointer;
  color: #fff;
  text-shadow: 0 1px #528009;
  vertical-align: bottom;
  background: #89c403 linear-gradient(#89c403 5%, #77a809 100%);
  border: 2px solid #e8e8e9;
  border-radius: 6px;
  padding: 4px 5px;
  font-family: Arial;
  font-size: 15px;
  font-weight: bold;
  text-decoration: none;
  display: inline-block;
  box-shadow: inset 0 1px #a4e271;
}

.btn:hover {
  background: #77a809 linear-gradient(#77a809 5%, #89c403 100%);
}

.load-more {
  margin-left: auto;
  margin-right: auto;
  display: block;
}

img {
  max-width: 100%;
  display: block;
}

.gallery {
  max-width: 1140px;
  grid-gap: 12px;
  grid-template-columns: repeat(auto-fit, minmax(320px, 1fr));
  grid-auto-rows: 240px;
  justify-content: center;
  margin-left: auto;
  margin-right: auto;
  padding-left: 15px;
  padding-right: 15px;
  list-style: none;
  display: grid;
  overflow: auto;
}

.gallery__item {
  position: relative;
  box-shadow: 0 1px 3px rgba(0, 0, 0, .2), 0 1px 1px rgba(0, 0, 0, .14), 0 2px 1px -1px rgba(0, 0, 0, .12);
}

.gallery__image:hover {
  transform: scale(1.03);
}

.gallery__image {
  height: 100%;
  width: 100%;
  object-fit: cover;
  transition: transform var(--animation-duration) var(--timing-function);
}

.gallery__link {
  height: 100%;
  text-decoration: none;
  display: block;
}

.gallery__link:hover {
  cursor: zoom-in;
}

.list-item {
  margin-bottom: 15px;
}

p {
  margin: 0;
  padding: 0;
}

.info {
  justify-content: space-evenly;
  font-size: 10px;
  display: flex;
}

a {
  all: unset;
}

.visually-hidden {
  white-space: nowrap;
  width: 1px;
  height: 1px;
  clip: rect(0 0 0 0);
  clip-path: inset(50%);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

.info-end {
  text-align: center;
  margin-left: auto;
  margin-right: auto;
  font-size: 15px;
  font-weight: 800;
}

